import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { TypeOfMedium } from '@models';
import { ApiService, SweetAlertService } from '@services';
import { ENDPOINTS } from '@shared';
import { MediumExecutionController } from 'app/core/models/medium-execution-controller.model';
import { TypeOfMediumExecution } from 'app/core/models/type-of-medium-execution.model';
import { ValidateFieldService } from 'app/core/services/validate-field.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-medium-execution-controller',
  templateUrl: './medium-execution-controller.component.html',
  styleUrls: ['./medium-execution-controller.component.scss']
})
export class MediumExecutionControllerComponent implements OnInit {
  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  dataSource: MatTableDataSource<MediumExecutionController>;
  displayedColumns: string[] = ['typeOfMediumController', 'typeOfExecutionController'];

  mediumExecutionController = new MediumExecutionController();
  typeOfMedium = new TypeOfMedium();
  typeOfMediumExecution = new TypeOfMediumExecution();
  typeOfMediumList = [];
  typeOfMediumExecutionList = [];
  mediumExecutionControllerList = [];
  mediumExecutionControllerForm: FormGroup;


  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    public validateFieldService: ValidateFieldService,
  ) { }

  ngOnInit(): void {
    this.setFormValidator();
    this.getMaintenance();
    this.getList();
  }

  getList(): void{
    this.apiService.findAll(ENDPOINTS.mediumExecutionController).subscribe((res:any) => {
      this.mediumExecutionControllerList = res.responseData.data;

      this.dataSource = new MatTableDataSource<MediumExecutionController>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    })
  }

  getMediumExecution(): void {
    this.apiService.findById(ENDPOINTS.mediumExecutionController, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.mediumExecutionController = res.responseData.data;
      }
    });
  }

  save(): void {
    this.apiService.save(ENDPOINTS.mediumExecutionController, this.mediumExecutionController).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator();
        this.getList();
        this.getMaintenance();
      });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.mediumExecutionController, this.mediumExecutionController).subscribe(
      (res: any) => {
        if (res !== undefined){
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator()
        this.getList();
        this.getMaintenance();
      });
  }


  getMaintenance(): void {
    this.apiService.findAll(ENDPOINTS.typeOfMedium).subscribe((res:any) => {
      this.typeOfMediumList = res.responseData.data;
    })
    this.apiService.findAll(ENDPOINTS.typeOfExecutions).subscribe((res:any) => {
      this.typeOfMediumExecutionList = res.responseData.data;
    })
  }

  compareTypeOfMedium(value: any, selected: any): boolean {
    if (value && selected) {
      return value.description === selected.description;
    }
  }

  compareTypeOfExecution(value: any, selected: any): boolean {
    if (value && selected) {
      return value.typeOfExecution === selected.typeOfExecution;
    }
  }

  setFormValidator(): void {
    this.mediumExecutionControllerForm = this.formBuilder.group({
      typeOfExecution: ['', Validators.required],
      typeOfMedium: ['', Validators.required],
    });
  }

  resetFormValidator(): void {
    this.mediumExecutionController = new MediumExecutionController();
    this.typeOfMedium = new TypeOfMedium();
    this.typeOfMediumExecution = new TypeOfMediumExecution();
    this.mediumExecutionControllerForm.markAsUntouched();
    this.mediumExecutionControllerForm.markAsPristine();
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this brand?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.mediumExecutionController, this.mediumExecutionController.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Medium execution controller successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
